import gsap from "gsap";

export default ()=>{
    let preloader = document.querySelector('.preloader'),
        composition = document.querySelector('.preloader__composition'),
        bg = document.querySelector('.preloader__bg'),
        rotation = gsap.timeline();

    rotation.to(bg, {
        rotation: 360,
        ease: "none",
        duration: 5,
        repeat: -1
    });

    (function destroyPreloader() {
        let status = preloader.getAttribute('data-destroy');

        if(!status) {
            window.setTimeout(destroyPreloader, 100);
            return;
        }
        gsap.to(composition, {
            scale: 120,
            ease: "none",
            duration: 1,
            onComplete: ()=>{
                rotation.pause();
                preloader.style.display = 'none';
            }
        });
        gsap.to(preloader, {
            opacity: 0,
            ease: "none",
            duration: 1
        })

    })();

}